//    
// 1.         ._wrap-to-animation    .
// 1.1         .     CSS    include../ lazy - load / lazy - load
//    background: url('../images/loading.gif') center / 50px no-repeat;
// 2.    data-src     .
// 3.      11px PNG ,  ,  src 
// 4.      data-map      ( "src" iframe ).     .     .
// 5.   picture/source/   data-srcset c    .    srcset   -    11px PNG ,  
// 6.  img(src="imagesNative/1x1clear.png", data-src="images/grid/cover-image-300x200.webp", alt="alt")
// 7.   https://www.youtube.com/watch?v=IdUbsiQUxa4  https://www.youtube.com/watch?v=-2kzokozC7E


// Lazy Load function

let lazyImages = document.querySelectorAll('img[data-src],source[data-srcset]');
//    
let windowHeight = document.documentElement.clientHeight;
// let loadMapBlock = document.querySelector('.');
let loadMoreBlock = document.querySelector('._load-more');

let lazyImagesPositions = [];
if (lazyImages.length > 0) {
    lazyImages.forEach(img => {
        if (img.dataset.src || img.dataset.srcset) {
            //        
            lazyImagesPositions.push(img.getBoundingClientRect().top + scrollY);
            lazyScrollCheck();
        }
    });
}

//  
window.addEventListener('scroll', lazyScroll);

function lazyScroll() {
    //       
    if (document.querySelectorAll('img[data-src],source[data-srcset]').length > 0) {
        lazyScrollCheck();
    }
}


function lazyScrollCheck() {
    let imgIndex = lazyImagesPositions.findIndex(
        //   ,      :    >   -    
        item => scrollY > item - windowHeight
    );
    // , - ,         src        data-src
    if (imgIndex >= 0) {
        if (lazyImages[imgIndex].dataset.src) {
            lazyImages[imgIndex].src = lazyImages[imgIndex].dataset.src;
            lazyImages[imgIndex].removeAttribute('data-src');
            lazyImages[imgIndex].parentElement.classList.remove('_wrap-to-animation')
        } else if (lazyImages[imgIndex].dataset.srcset) {
            lazyImages[imgIndex].srcset = lazyImages[imgIndex].dataset.srcset;
            lazyImages[imgIndex].removeAttribute('data-srcset');
            lazyImages[imgIndex].parentElement.classList.remove('_wrap-to-animation')
        }
        delete lazyImagesPositions[imgIndex];
    }
}

function loadMore() {
    const loadMoreBlockPos = loadMoreBlock.getBoundingClientRect().top + scrollY;
    const loadMoreBlockHeight = loadMoreBlock.offsetHeight;

    if (scrollY > (loadMoreBlockPos + loadMoreBlockHeight) - windowHeight) {
        getContent();
    }
}


